"use client";

import { useEffect, useState } from 'react';
import Link from 'next/link';
import Card from '../../components/Card';
import Spinner from '../../components/Spinner';
import Badge from '../../components/Badge';
import Button from '../../components/Button';

interface Clinic {
  _id: string;
  name: string;
  address: {
    street: string;
    city: string;
    governorate: string;
  };
  phone: string;
  operating_hours: string;
}

export default function ClinicsPage() {
  const [clinics, setClinics] = useState<Clinic[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    fetchClinics();
  }, []);

  const fetchClinics = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/clinics');
      if (!response.ok) throw new Error('Failed to fetch');
      const data = await response.json();
      setClinics(data);
    } catch (err: any) {
      setError(err.message || 'Error fetching clinics');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-slate-50 via-white to-slate-50">
      <div className="max-w-7xl mx-auto px-4 lg:px-8 py-16 lg:py-24">
        {/* Header */}
        <div className="mb-16 space-y-4">
          <div className="inline-flex items-center px-4 py-2 rounded-full bg-indigo-100 text-indigo-700 text-sm font-semibold gap-2">
            <span>üè•</span> Healthcare Facilities
          </div>
          <h1 className="text-5xl lg:text-6xl font-bold text-slate-900">Browse Clinics</h1>
          <p className="text-xl text-slate-600 max-w-2xl leading-relaxed">Find verified clinics in your area with real-time availability and instant booking.</p>
        </div>

        {/* Controls */}
        <div className="flex justify-between items-center mb-12 bg-white rounded-xl p-6 shadow-sm border border-slate-100">
          <div className="space-y-1">
            <p className="text-sm text-slate-500">Available Clinics</p>
            <p className="text-2xl font-bold text-slate-900">{clinics.length}</p>
          </div>
          <Button variant="secondary" size="sm" onClick={fetchClinics} className="group">
            <span className="mr-2 group-hover:rotate-180 transition-transform duration-300">üîÑ</span>
            Refresh
          </Button>
        </div>

        {/* Content */}
        {loading ? (
          <div className="flex flex-col items-center justify-center py-24 space-y-4">
            <Spinner className="h-12 w-12 text-indigo-600" />
            <p className="text-slate-600 font-medium">Loading clinics...</p>
          </div>
        ) : error ? (
          <div className="bg-gradient-to-r from-red-50 to-orange-50 border border-red-200 rounded-2xl p-8 text-center">
            <p className="text-red-700 font-semibold text-lg">‚ö†Ô∏è {error}</p>
            <Button onClick={fetchClinics} className="mt-4">Try Again</Button>
          </div>
        ) : clinics.length === 0 ? (
          <div className="bg-white rounded-2xl border-2 border-dashed border-slate-300 p-16 text-center space-y-4">
            <p className="text-5xl">üè•</p>
            <p className="text-slate-600 text-lg font-medium">No clinics found yet</p>
            <p className="text-slate-500">Try searching again or refreshing the page.</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {clinics.map((clinic) => (
              <Link key={clinic._id} href={`/clinics/${clinic._id}`} className="group h-full">
                <Card className="h-full hover:shadow-2xl transition-all duration-300 hover:-translate-y-1 p-8 space-y-6 border border-slate-100 hover:border-indigo-300 bg-white">
                  {/* Top Section */}
                  <div className="flex items-start justify-between">
                    <div className="flex-1 space-y-2">
                      <h2 className="text-xl font-bold text-slate-900 group-hover:text-indigo-600 transition-colors line-clamp-2">
                        {clinic.name}
                      </h2>
                      <p className="text-sm text-slate-500 font-medium">Medical Facility</p>
                    </div>
                    <div className="text-4xl">üè•</div>
                  </div>

                  {/* Info Section */}
                  <div className="space-y-3 py-4 border-y border-slate-100">
                    <div className="flex items-start gap-3">
                      <span className="text-base shrink-0">üìç</span>
                      <div className="space-y-0.5">
                        <p className="text-sm font-semibold text-slate-800">{clinic.address.city}</p>
                        <p className="text-xs text-slate-600">{clinic.address.street}</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-3">
                      <span className="text-base">üìû</span>
                      <a href={`tel:${clinic.phone}`} className="text-sm font-semibold text-indigo-600 hover:text-indigo-700 underline">
                        {clinic.phone}
                      </a>
                    </div>
                    <div className="flex items-center gap-3">
                      <span className="text-base">üïê</span>
                      <span className="text-sm text-slate-600">{clinic.operating_hours}</span>
                    </div>
                  </div>

                  {/* Bottom Section */}
                  <div className="flex items-center justify-between pt-2">
                    <Badge variant="gray">{clinic.address.governorate}</Badge>
                    <span className="text-indigo-600 font-bold group-hover:translate-x-1 transition-transform">‚Üí</span>
                  </div>
                </Card>
              </Link>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
